.pricing__section{
    padding: 100px 0 160px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    background:white;
}

.pricing__container-card{
    background: linear-gradient(45deg,#0660e9 0%,#276afb 100%);
    box-shadow: 0 6px 20px rgba(56, 125, 255, 0.2);
    height: 500px;
    text-decoration: none;
   border-radius: 4px;
}
.pricing__container-card{
    transform: scale(1.05);
    background: black;
    margin: 0 24 px;

}

.pricing__container-card:hover{
    transform: scale(1.06);
    transition: all 0.3s ease-out;
    color: black;
}
.pricing-container{
    display: flex;
    justify-content: center;
    align-items: center;

}
.pricing__wrapper{
    display: flex;
    flex-direction: column;
    align-items: start;
    margin: 0 auto;

}
.pricing__heading{
    color:#1c2237;
    margin-bottom: 24px;
}
.pricing__container-cardInfo{
    display: flex;
    flex-direction: column;
    height: 500px;
    padding: 24px;
    align-items: center;
    color: #fff;
}
.pricing__container-cardInfo h3{
    margin-bottom: 5px;
    font-size: 24px;
}
.pricing__container-cardInfo h4{
    font-size: 40px;
}
.pricing__container-cardInfo p{
    font-size: 40px 14px;
    margin-bottom: 24px;
}
.pricing__container-features{
    margin: 16px 0 32px;
    list-style: none;
    display:flex;
    flex-direction: column;
    align-items: center;
}
.pricing__container-features li{
   margin-bottom: 10px;

}
.icon{
    margin: 24px 0;;
}
@media screen and (max-width){
    .pricing__container{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
    }
    .pricing__container-card{
        width: 900%;
    }
    .pricing__wrapper{
        margin: 0 30px;
  display: flex;
  flex-direction: column;
  align-items: center;

    }
    .pricing__container-card:nth-child(2){
        transform: scale();
        background: #242424;
        margin: 24px;
    }
    .pricing__container-card:hover{
       transform: none;
    }
}