.home__hero-section{
    color: #fff;
    padding: 160px 0;
}
.home__hero-row{
    align-items: center; 
 }
.row{
    display: flex;
    margin-right: -15px;
    margin-bottom: -15px;
    margin-left: -15px;
    flex-wrap: wrap;
    align-content: stretch;
}

.col{
    margin-bottom: 15px;
    padding-right: 15px;
    padding-left: 15px;
    flex:1;
    max-width: 50%;
    flex-basis: 50%;
}
.home__hero-text-wrapper{
    max-width: 540px;
    padding: 0;
    padding-bottom: 60px;


}
.top-line{
color: red;
line-height: 16px;
font-weight: 700;
letter-spacing: 1.4px;
text-transform:uppercase;
margin-bottom: 16px;
}
.heading{
    margin-bottom:24px;
    font-size: 48px;
    line-height: 1.1;
    font-weight: 600;
    color: white;

}

.dark{
    color: black;
}
.darkBg{
    background-color: #1c2237;
}
.home__hero-subtitle{
    max-width: 440px;
    margin-bottom:35px;
    font-size: 18px;
    line-height: 24px;


}
.home__hero-img-wrapper{
    max-width: 555px;

}
.home__hero-img{
    max-width: 95%;
    margin-top: 0;
    margin-left: 0;
    margin-left: 10px;
    padding-right: 0;
}
.home__hero-img{
    border: 0;
    max-width: 100%;
    vertical-align:middle;
    display: inline-block;
}

@media screen and (max-width:991px){
    .container{
        padding-right:30px ;
        padding-left: 30px;
    }
}

@media screen and (max-width:768px) {
    .home__hero-text-wrapper{
        padding-bottom: 65px;
    
    }
    .col{
        max-width: 100%;
        flex-basis:100%
        /* shearch on google . what is flex basis */
    }
}